import { Tabs, Toast } from "@taroify/core"
import { MoreOutlined } from "@taroify/icons"
import * as React from "react"
import Block from "../../../components/block"
import Page from "../../../components/page"
import "./index.scss"

function BasicTabs() {
  return (
    <Tabs>
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
      <Tabs.TabPane title=" 4"> 4</Tabs.TabPane>
    </Tabs>
  )
}

function KeyedTabs() {
  return (
    <Tabs defaultValue="a">
      <Tabs.TabPane value="a" title=" 1">
         1
      </Tabs.TabPane>
      <Tabs.TabPane value="b" title=" 2">
         2
      </Tabs.TabPane>
      <Tabs.TabPane value="c" title=" 3">
         3
      </Tabs.TabPane>
      <Tabs.TabPane value="d" title=" 4">
         4
      </Tabs.TabPane>
    </Tabs>
  )
}

function ScrollTabs() {
  return (
    <Tabs>
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
      <Tabs.TabPane title=" 4"> 4</Tabs.TabPane>
      <Tabs.TabPane title=" 5"> 5</Tabs.TabPane>
      <Tabs.TabPane title=" 6"> 6</Tabs.TabPane>
      <Tabs.TabPane title=" 7"> 7</Tabs.TabPane>
    </Tabs>
  )
}

function DisableTabs() {
  return (
    <Tabs>
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2" disabled>
         2
      </Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
    </Tabs>
  )
}

function CardTabs() {
  return (
    <Tabs theme="card">
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
    </Tabs>
  )
}

function TabsWithTabClick() {
  return (
    <>
      <Toast id="toast" />
      <Tabs onTabClick={({ title }) => Toast.open(title)}>
        <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
        <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
        <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
      </Tabs>
      <Toast id="toast" />
    </>
  )
}

function StickyTabs() {
  return (
    <Tabs sticky>
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
      <Tabs.TabPane title=" 4"> 4</Tabs.TabPane>
    </Tabs>
  )
}

function TabsWithCustomTitle() {
  return (
    <Tabs>
      <Tabs.TabPane
        title={
          <>
            <MoreOutlined />  1
          </>
        }
      >
         1
      </Tabs.TabPane>
      <Tabs.TabPane
        title={
          <>
            <MoreOutlined />  2
          </>
        }
      >
         2
      </Tabs.TabPane>
      <Tabs.TabPane
        title={
          <>
            <MoreOutlined />  3
          </>
        }
      >
         3
      </Tabs.TabPane>
    </Tabs>
  )
}

function AnimatedTabs() {
  return (
    <Tabs animated>
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
      <Tabs.TabPane title=" 4"> 4</Tabs.TabPane>
    </Tabs>
  )
}

function SwipeableTabs() {
  return (
    <Tabs animated swipeable>
      <Tabs.TabPane title=" 1"> 1</Tabs.TabPane>
      <Tabs.TabPane title=" 2"> 2</Tabs.TabPane>
      <Tabs.TabPane title=" 3"> 3</Tabs.TabPane>
      <Tabs.TabPane title=" 4"> 4</Tabs.TabPane>
    </Tabs>
  )
}

export default function TabsDemo() {
  return (
    <Page title="Tabs " className="tabs-demo">
      <Block title="">
        <BasicTabs />
      </Block>
      <Block title="">
        <KeyedTabs />
      </Block>
      <Block title="">
        <ScrollTabs />
      </Block>
      <Block title="">
        <DisableTabs />
      </Block>
      <Block title="">
        <CardTabs />
      </Block>
      <Block title="">
        <TabsWithTabClick />
      </Block>
      <Block title="">
        <StickyTabs />
      </Block>
      <Block title="">
        <TabsWithCustomTitle />
      </Block>
      <Block title="">
        <AnimatedTabs />
      </Block>
      <Block title="">
        <SwipeableTabs />
      </Block>
    </Page>
  )
}
